/*Applies styles to both footers*/
.ucpFooter, .MobileFooter {
    width: 100%;
    background-image: url('../../images/Footer.png');
    background-position: center;
    background-size: cover;
    bottom: 0;
    margin: 0px;
    position: fixed;
}

/*Applies styles to desktop footer*/
.ucpFooter{
    height: 145px;
    background-repeat: no-repeat;
}

/*Applies styles to mobile footer*/
.MobileFooter{
    height: 145px;
    background-repeat: no-repeat;
    background-size: cover;
}


